
.popover1{
  font-family: ProximaNova-Reg-Cond;
  font-weight: bold;
  font-size: 18;
  position: absolute;
  z-index: 1060;
  color: black;
  width: 40%;
  height: 28%;
  padding: 1px;
  left: auto;
  right: 15px;
  top: 15px;
  background-color:white;
  -webkit-background-clip: padding-box;
  background-clip: padding-box;
  border: 1px solid #ccc;
  border: 1px solid rgba(0, 0, 0, .2);
  border-radius: 0px !important;
  -webkit-box-shadow: 0 5px 10px rgba(0, 0, 0, .2);
}

.popover1 > .left {  
  margin-left: -10px;
}
.popover1 > .arrow,
.popover1 > .arrow:after {
  position: absolute;
  display: block;
  width: 0;
  height: 0;
  border-color: transparent;
  border-style: solid;
}

.popover1 > .arrow {
  border-width: 11px;
}

.popover1 > .arrow:after {
  content: "";
  border-width: 10px;
}

.popover1.left > .arrow {
  top: 50%;
  right: -11px;
  margin-top: -11px;
  border-right-width: 0;
  border-left-color: #999;
  border-left-color: rgba(0, 0, 0, .25);
}

.popover1.left > .arrow:after {
  right: 1px;
  bottom: -10px;
  content: " ";
  border-right-width: 0;
  border-left-color: #2a4254;
}

.listGroup {
  position: absolute;
  left: 0px;
  right: 0px;
  top: 37px;
  bottom: 0px;
}

.list-group-item{
  font-family: ProximaNova-Reg-Cond;
  color: #333333;
  font-size: 16px;	
  font-weight: 600;	
  line-height: 20px;
}