body {
  margin: 0;
  padding: 0;
  overflow-y:hidden;
  font-family: ProximaNova-Reg;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.sub-container {
  height: calc(100vh - 160px);
  width: 100%;
  background-color: black;
}
.zero-border {
  border-radius: 0px;
  border : 0px;
}
.zero-padding {
  padding-right: 0px;
  padding-left: 0px;
}
.footer-row {
  position:absolute;
  bottom:0;
}
.logo {
  width: 112px;
  padding: 10px;
}